<template>
  <div class="create-profile">
    <div class="container">
        <div class="row">
            <div class="col-md-8 m-auto">
                <a class="btn btn-light" @click.prevent="$router.go(-1)"></a>
                <h1 class="display-4 text-center"></h1>
                <p class="lead text-center">, !</p>
                <small class="d-block pb-3">* </small>
               <form @submit.prevent="submit">
                 <TextField 
                  type='text'
                  name='handle'
                  placeholder="* Profile Handle"
                  v-model="msgInfo.handle"
                  :error='errors.handle'
                  info='handle, email'
                ></TextField>

                <SelectList
                  name='status'
                  :error='errors.status'
                  v-model="msgInfo.status"
                  :options='options'
                  info=''
                ></SelectList>
                
                <TextField 
                  type='text'
                  name='company'
                  placeholder=""
                  v-model="msgInfo.company"
                  :error='errors.company'
                  info=''
                ></TextField>

                <TextField 
                  type='text'
                  name='website'
                  placeholder=""
                  v-model="msgInfo.website"
                  :error='errors.website'
                  info=''
                ></TextField>

                <TextField 
                  type='text'
                  name='location'
                  placeholder=""
                  v-model="msgInfo.location"
                  :error='errors.location'
                  info=' (. )'
                ></TextField>

                <TextField 
                  type='text'
                  name='skills'
                  placeholder=""
                  v-model="msgInfo.skills"
                  :error='errors.skills'
                  info=' (: HTML,CSS,JavaScript,PHP)'
                ></TextField>

                <TextField 
                  type='text'
                  name='githubusername'
                  placeholder="Github "
                  v-model="msgInfo.githubusername"
                  :error='errors.githubusername'
                  info=', github'
                ></TextField>

               <TextArea
                placeholder=""
                name='bio'
                v-model="msgInfo.bio"
                :error='errors.bio'
                info=''
               ></TextArea>
              
               <div class="mb-3">
                <button type="button" class="btn btn-light" 
                  @click="displaySocialInputs = !displaySocialInputs"></button>
                <span class="text-muted"></span>
               </div>
              
               <div v-show="displaySocialInputs">
                 <InputGroup
                  placeholder=""
                  name='wechat'
                  v-model="msgInfo.wechat"
                  :error='errors.wechat'
                  icon='fab fa-weixin'
                 ></InputGroup>

                 <InputGroup
                  placeholder="QQ"
                  name='QQ'
                  v-model="msgInfo.QQ"
                  :error='errors.QQ'
                  icon='fab fa-qq'
                 ></InputGroup>

                 <InputGroup
                  placeholder=""
                  name='tengxunkt'
                  v-model="msgInfo.tengxunkt"
                  :error='errors.tengxunkt'
                  icon='fab fa-wechat'
                 ></InputGroup>

                 <InputGroup
                  placeholder=""
                  name='wangyikt'
                  v-model="msgInfo.wangyikt"
                  :error='errors.wangyikt'
                  icon='fab fa-wechat'
                 ></InputGroup>
               </div>

                <input type="submit" class="btn btn-info btn-block mt-4" />
              </form>
            </div>
        </div>
    </div>
  </div>

</template>

<script>
import TextField from "./common/TextFieldGroup";
import TextArea from "./common/TextAreaGroup";
import SelectList from "./common/SelectListGroup";
import InputGroup from "./common/InputGroup";

export default {
  name: "create-profile",
  data() {
    return {
      msgInfo: {
        handle: "",
        company: "",
        website: "",
        location: "",
        status: "* ",
        skills: "",
        githubusername: "",
        bio: "",
        wechat: "",
        QQ: "",
        tengxunkt: "",
        wangyikt: ""
      },
      options: [
        { label: "0", value: "* " },
        { label: "Junior Developer", value: "" },
        { label: "Senior Developer", value: "" },
        { label: "HighDeveloper", value: "" },
        { label: "Manager", value: "" },
        { label: "backend Developer", value: "" },
        { label: "Python machine learning", value: "Python" },
        { label: "Other", value: "" }
      ],
      errors: "",
      displaySocialInputs: false
    };
  },
  components: {
    TextField,
    TextArea,
    SelectList,
    InputGroup
  },
  created() {},
  methods: {
    submit() {
      console.log(this.msgInfo);

      this.$axios
        .post("/api/profile", this.msgInfo)
        .then(res => {
          this.errors = ""; // 
          this.$store.dispatch("setProfile", res.data);
          this.$router.push("/dashboard");
        })
        .catch(error => {
          if (error.response.data) {
            this.errors = error.response.data;
          }
        });
    }
  }
};
</script>

<style>
</style>



