import Vue from 'vue';
import Vuex from 'vuex';
import * as getters from './getter'
import * as mutations from './mutations'
import * as actions from './actions'

Vue.use(Vuex);

const state = { // 
    isAutnenticated: false,  // 
    user: {},   // 
    profile: {}, // 
    loading: false,
    profiles: [] // 
}


export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions
})


